@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@400;700&family=Roboto:wght@400;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.main {
  font-family: Roboto, sans-serif;
}

.main .sectionImg img {
  width: 100%;
}

.main .contentTitle {
  text-align: center;
  margin: 30px;
  font-weight: 700;
}

.main .contentText {
  text-align: center;
  font-size: 20px;
  margin: 0 20px;
}

.mainBtn {
  display: flex;
  justify-content: center;
}

#myBtn {
  width: 160px;
  height: 60px;
  /* Button Text */
  font-family: Roboto, sans-serif;
  font-style: normal;
  text-align: center;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  background: #e4c1ae;
  color: #FFFFFF;
  margin: 30px;
  margin-bottom: 50px;
  border-radius: 32px;
  border: none;
}

.closeBtn {
  font-family: Roboto, sans-serif;
  font-style: normal;
  text-align: center;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  background: #e4c1ae;
  color: #FFFFFF;
  padding: 10px;
  margin-bottom: 50px;
  border-radius: 32px;
  border: none;
}

#myBtn {
  padding: 10px 15px;
}

/* The Modal (background) */
.modal {
  display: none;
  /* Hidden by default */
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  position: relative;
  background-color: #fefefe;
  margin: 15% auto;
  /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  /* Could be more or less, depending on screen size */
}

/* The Close Button */
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

/* The Modal (background) */
.modalAlert {
